import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';

import { Panel, PanelHeader, Header, Button, Group, Cell, Div, Avatar, CellButton, Title } from '@vkontakte/vkui';

import './game.css';

const Attempt = ({attempts, setAtmp}) => {
	return(
		<div>
			<Title level="2" weight="medium" id="count">Попыток: {attempts+1}</Title>
		</div> 
	);
}

function GenWord({word, gameKey}){
	const _keyboard_ = word.split("").map((key, index) => 
		<div id="gfklj244" key={key+index} className="letter borrad m-5 bg-block">&nbsp;</div>
	);

	return (
		<div>
			<div className="row-key">{_keyboard_}</div>
			{!gameKey ? <span>Это слово: {word}</span> : ""}
		</div>
	)
}

const Keyboard = ({keys, clk}) => {
	let rnd = Math.floor(Math.random()*1234353)
	const _keyboard_ = keys.split("").map((key) =>
		<div id="dhjgf432" className="flex-1 m-5 borrad letter button bg-block" key={key} aria-label="{key}" onClick={clk}>{key}</div>
	);

	return <div className="row-key">{_keyboard_}</div>
}

const NextKey = ({keys, clk}) => {
	return <div className="row-key">
		<div id="dhjgf432" className="flex-1 m-5 borrad letter button bg-block" key={keys+22321} aria-label="{keys}" onClick={clk}>{keys}</div>
	</div>
}

const new_word = () =>{
	return ["парень", "школа", "армия", "любовь"]
	// var xhr = new XMLHttpRequest();
	// xhr.open('GET', 'https://botpoliteh.ru/static_dir/newFile.txt', false);
	// xhr.send();
	// return xhr.responseText.split("\r\n")
};

const generateWord = (words) => {
	// let words = ["парень", "школа", "армия", "любовь"]
	let rnd = Math.floor(Math.random()*words.length)
	return words[rnd]
}

const this_ok = (key, word) => {
	const arr_w = word.split("") 
	if (arr_w.includes(key.innerText.toLowerCase())){
		var pos = 0
		for (var i of arr_w){
			if (i === key.innerText.toLowerCase()){
				document.querySelectorAll("#gfklj244")[pos].innerText = key.innerText;
			}
			pos++
		}
		return true
	}
	return false
}

const game_over = (attempts,word) => {
	let a = ''; document.querySelectorAll("#gfklj244").forEach((e) => {a+=e.innerText})
	if (a.toLowerCase() == word) return false
	if (attempts == 0) return false
	return true
}

const GenKeyboard = ({game, word, setWord, words}) => {
	const [attempts, setAtmp] = useState(5);
	const [gameKey, setgameKey] = useState(true);


	const updateAtmp = (event) => {
		let a = this_ok(event.target, word)
		!a ? (
			event.target.classList.toggle("disable"),
			setAtmp(prev => prev - 1)
		) : event.target.classList.toggle("okey")
		!game_over(attempts, word) ? setgameKey(false) : ""
	}

	const updateGame = () => {
		document.querySelectorAll("#gfklj244").forEach((e) => e.innerText = "")
		setgameKey(true)
		setAtmp(5)
		setWord(generateWord(words))
		game[1](false)
	}

	return(
		<Div>
			<Attempt attempts={attempts} setAtmp={setAtmp}/>
			<Div id="word" className="column-">
				<GenWord word={word} gameKey={gameKey}/>
			</Div>
			<div className="page flex-1">
				<div id="keyboard">
					{gameKey ?
						<Div><Keyboard keys={"йцукенгшщзхъ"} clk={updateAtmp}/>
						<Keyboard keys={"фывапролджэ"} clk={updateAtmp}/>
						<Keyboard keys={"ячсмитьбю"} clk={updateAtmp}/></Div>
					: 
						<Div className="nextBlock"><Title level="1" weight="medium" className="p-5">Продолжить?</Title>
						<NextKey keys={"Далее"} clk={updateGame}/></Div>
					}
				</div>
			</div>
		</Div>
	)

}

const Game = ({id, go}) => {
	const words = new_word()
	const [word, setWord] = useState(generateWord(words))
	const [game, setGame] = useState(true)

	return(
		<Panel id={id}>
			<PanelHeader className="myClass">Отгадай слово</PanelHeader>
			<GenKeyboard game={[game, setGame]} word={word} setWord={setWord} words={words}/>
		</Panel>
	);
}


Game.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Game;
